<style lang="less">
#layout-tabs {
  .ant-tabs {
    .ant-tabs-bar {
      margin: 0;
    }
  }
}
</style>

<template>
  <div id="layout-tabs">
    <!-- <div :style="{ marginBottom: '16px' }">
      <a-button @click="add">ADD</a-button>
    </div> -->
    <a-tabs hideAdd v-model="activeKey" type="editable-card" @edit="onEdit" @tabClick='tabClick'>
      <a-tab-pane v-for="pane in panes" :tab="pane.title" :key="pane.key" :closable="pane.closable" />
    </a-tabs>
  </div>

</template>
<script>
import utils from "@/utils";
const _utils = new utils();

export default {
  data() {
    return {
      activeKey: "0",
      panes: [],
      newTabIndex: 0
    };
  },
  created() {
    this.panes = _utils.getNavTabs();
    let routerArr = this.panes.filter(a => a.routerName === this.$route.name);
    this.activeKey = routerArr[0].key;
  },
  watch: {
    $route: function() {
      this.panes = _utils.getNavTabs();
    }
  },
  methods: {
    tabClick(key) {
      this.$router.push({ name: this.panes[key].routerName });
    },
    callback() {
      // console.log(key);
    },
    onEdit(targetKey, action) {
      this[action](targetKey);
    },
    add() {
      const panes = this.panes;
      const activeKey = `newTab${this.newTabIndex++}`;
      panes.push({
        title: `New Tab ${activeKey}`,
        content: `Content of new Tab ${activeKey}`,
        key: activeKey
      });
      this.panes = panes;
      this.activeKey = activeKey;
    },
    remove(targetKey) {
      let activeKey = this.activeKey;
      let lastIndex;
      this.panes.forEach((pane, i) => {
        if (pane.key === targetKey) {
          lastIndex = i - 1;
        }
      });
      const panes = this.panes.filter(pane => pane.key !== targetKey);
      let _panes;
      if (lastIndex >= 0 && activeKey === targetKey) {
        _panes = panes[lastIndex];
        activeKey = panes[lastIndex].key;
      }
      this.panes = panes;
      this.activeKey = activeKey;
      this.$router.push({ name: _panes.routerName });
    }
  }
};
</script>