import localStorage from './localStorage'
import config from '@/config'
const _localStorage = new localStorage();
export default class utils {
  constructor() {

  }
  removeNavTabs(){
    
  }
  getNavTabs() {
    return _localStorage.getLocalStorage(config.localStorage.navTabs);
  }
  setNavTabs(obj) {
    let navTabs = _localStorage.getLocalStorage(config.localStorage.navTabs);
    let defaultTab = {
      title: "首页",
      key: "0",
      closable: false,
      routerName: "dashboard"
    }
    if (navTabs.filter(a => a.routerName === defaultTab.routerName).length === 0) {
      navTabs.push(defaultTab)
    }

    if (navTabs.filter(a => a.routerName === obj.routerName).length === 0) {
      obj.key = navTabs.length + '';
      navTabs.push(obj)
    }
    let result = navTabs;
    _localStorage.setLocalStorage(config.localStorage.navTabs, result);
  }
}