import Layout from '@/components/Layout'
export default [
  {
    path: '/login',
    name: 'login',
    meta: {
      title: 'Login - 登录',
    },
    component: () => import('@/pages/login')
  },
  {
    path: '/',
    name: '_home',
    redirect: '/dashboard',
    component: Layout,
    children: [
      {
        path: '/dashboard',
        name: 'dashboard',
        meta: {
          title: '首页',
        },
        component: () => import('@/pages/dashboard')
      }
    ]
  },
  {
    path: '/accounting',
    name: '_accounting',
    redirect: 'accounting/dataexport',
    component: Layout,
    children: [
      {
        path: 'bonusdata',
        name: 'accounting_bonusdata',
        meta: {
          title: '奖励金数据',
        },
        component: () => import('@/pages/accounting/bonusdata')
      },
      {
        path: 'bonusdata/create',
        name: 'accounting_bonusdata_create',
        meta: {
          title: '奖励金数据-添加',
        },
        component: () => import('@/pages/accounting/bonusdata/createOrEdit')
      },
      {
        path: 'dataexport',
        name: 'accounting_dataexport',
        meta: {
          title: '奖励金数据导出',
        },
        component: () => import('@/pages/accounting/dataexport')
      }
    ]
  },
]